import React, { Component } from 'react'


import EmployeeService from '../services/EmployeeService';
class CreateLanguage extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            languageName: '',
        }
        this.changeLanguageNameHandler = this.changeLanguageNameHandler.bind(this);
        this.saveOrUpdateEmployee = this.saveOrUpdateEmployee.bind(this);

    }

    // step 3
    componentDidMount() {

        // step 4
        if (this.state.id === '_add') {
            return
        }
    }
    backToAdminPage = (e) => {
        e.preventDefault();
        alert("hello");
        window.location.replace("http://localhost:8080/adminPage");
    }
    saveOrUpdateEmployee = (e) => {
        e.preventDefault();
        let language = { languageName: this.state.languageName }
        console.log('language => ' + JSON.stringify(language));
        EmployeeService.addLanguage(language).then(res => {

            let lang = res.data;    // employee variable has the data which is retured from controller
            // it will print the data from controller 
            var comp = JSON.stringify(lang);
            if (comp === '"success"') {
                document.getElementById('resvalue').innerHTML = "Language Added Successfully";
            }
            else {
                document.getElementById('resvalue').innerHTML = "Language Already Exists";
            }
        });
    }

    changeLanguageNameHandler = (event) => {
        this.setState({ languageName: event.target.value });
    }



    /*
    */



    render() {
        return (
            <div >
                <h1 className="text-center" style={{ marginTop: `150px`, color: 'white' }}><b>Language Form</b></h1>
                <br></br>
                <div className="container" style={{ marginRight: `380px`, width: `800px` }}>
                    <div className="row" style={{ width: `1100px`, height: `300px` }}>
                        <div className="card col-md-6 offset-md-3 offset-md-3">
                            {

                            }

                            <div className="card-body" >
                                <form>
                                    <div className="form-group">
                                        <h3 style={{ marginTop: `50px` }}>Language </h3>
                                        <input placeholder="Enter Language " name="languageName" style={{ marginTop: `20px`, height: `50px` }} className="form-control"
                                            value={this.state.languageName} onChange={this.changeLanguageNameHandler} />
                                    </div>

                                    <div className="form-group"  >
                                        <h4 id="resvalue"></h4>
                                    </div>


                                    <div className="card-body" style={{ marginLeft: "30%" }} >
                                        <button style={{ height: `50px` }} className="btn btn-success" onClick={this.saveOrUpdateEmployee}>Submit</button>
                                        <button style={{ height: `50px`, marginLeft: "10px" }} className="btn btn-danger" onClick={this.backToAdminPage} >Cancel</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default CreateLanguage
